.SearchBody {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.CompleteBar {
    width: 42.082%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.BAR {
    margin: 0 auto;
    width: 100%;
    border-radius: 30px;
    background-color: #fff;
    border: 1px solid #8991a5;
    display: flex;
    justify-content: center;
    height: 45px;
}

.BAR:hover {
    box-shadow: 1px 1px 8px 1px #dcdcdc;
}
.BAR:focus-within {
    box-shadow: 1px 1px 8px 1px #dcdcdc;
    outline: none;
}
.searchingbar {
    height: 100%;
    border: none;
    width: 86.956%;
    font-size: 16px;
    outline: none;
    font-family: "Montserrat";
}

.searchList {
    margin-top: 2px;
    width: 300px;
    z-index: 100;
    position: fixed;
    top: 36.25%;

    background-color: #eaeff2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    overflow-y: auto;
}
.searchItem {
    text-decoration: none !important;
    cursor: pointer !important;
}
.searchList::-webkit-scrollbar {
    display: none;
}
.searchItem {
    font-family: "Montserrat", sans-serif;
    font-size: small;
}
.searchList .searchItem {
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    color: black;
    font-family: "Montserrat";
    justify-content: center;
    text-align: center;
}

.searchItem:hover {
    background-color: #303030;
    color: #fff !important;
}

.search-button {
    border-color: transparent;
    background-color: transparent;
    width: 3.478%;
    margin: 0;
    padding: 0;
}
.NotFound {
    font-family: "Montserrat", sans-serif;
    color: #fff;
}
.search-button:active {
    border-color: #8991a5;
}
.fas {
    margin: 0;
    padding: 0;
}
@media (max-width: 760px) {
    .CompleteBar {
        width: 400px;
    }
    .BAR {
        width: 400px;
    }
}

@media (max-width: 440px) {
    .CompleteBar {
        width: 330px;
    }
    .BAR {
        width: 330px;
    }
}

.logo {
    width: 130px;
    height: 55.59px;
    margin-top: 4px;
    margin-bottom: 15px;
    margin-left: 10px;
}
@keyframes bouncy {
    0% {
        top: 0em;
    }
    40% {
        top: 0em;
    }
    43% {
        top: -0.9em;
    }
    46% {
        top: 0em;
    }
    48% {
        top: -0.4em;
    }
    50% {
        top: 0em;
    }
    100% {
        top: 0em;
    }
}
.bouncy {
    animation: bouncy 5s infinite linear;
    position: relative;
}

.header-btn1 {
    margin-top: 15px;
    text-decoration: none !important;
    color: #fff !important;
    border-radius: 0.3em;
    float: right;
    font-weight: 600;
    padding: 5px 15px;
    background: #d62828;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
}
.header-btn2 {
    background-color: transparent;
}

.header {
    height: 10%;
    margin-bottom: 10px;
    /* display: flex;
    justify-content: space-between;
    align-items: center; */
}

@media (max-width: 650px) {
    .logo {
        width: 120px;
    }
    .header-btn1 {
        padding: 4px 10px;
    }
}
@media (max-width: 450px) {
    .logo {
        margin-top: 8px;
        width: 100px;
        height: 45px;
    }
    .header-btn1 {
        padding: 3px 8px;
        font-size: small;
        margin-top: 18px;
    }
}

.footer {
    background: #000;
    padding: 2px 0px;
    font-family: "Montserrat", sans-serif;
    text-align: center;
}

.footer .row {
    width: 100%;
    margin: 0.5% 0%;
    padding: 0.6% 0%;
    color: gray;
    font-size: 0.9em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer .row a {
    text-decoration: none;
    color: gray;
    transition: 0.5s;
    font-size: 1.1em;
}

.footer .row a:hover {
    color: #fff;
}

.footer .row a {
    width: 30px;

    margin-left: 3px;
    margin-right: 3px;
}
.footer .row a i {
    width: 30px;
    height: 9px;
}

@media (max-width: 720px) {
    .footer {
        text-align: left;
        padding: 5%;
    }
    .footer .row ul li {
        display: block;
        margin: 10px 0px;
        text-align: left;
    }
    .footer .row a i {
        margin: 0% 3%;
    }
}
@media (max-width: 650px) {
    #row1 {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
    #row2 {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}

.HomePage {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.35)),
        url(/static/media/wallpaper.97ca5861.jpg);
    width: 100%;
    height: 100%;
    background-size: cover;
    position: relative;
    padding-bottom: 0;
    margin-bottom: 0;
    background-color: #0f1014;
}
.HomeSearch {
    margin-top: 20vh;
}
.genreHeader {
    color: #fff !important;
    text-align: center;
    font-family: "Montserrat";
    margin-top: 100px;
}
.buttonGrid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 auto;
    margin-top: 2px;
    margin-bottom: 0px !important;
    padding-bottom: 20px;
    width: 60%;
    margin-bottom: 6.5vh;
    transform: 0.2s all;
}

.HomeFooter {
    position: "fixed";
    left: 0;
    bottom: 0;
    right: 0;
}

.genreTagON {
    padding: 10px 20px;
    color: black !important;
    background-color: #edf2f4;
    border-radius: 20px;
    margin-left: 3px;
    margin-right: 3px;
    cursor: pointer;
    margin: 4px 5px;
}
.genreTagON:hover {
    background-color: #adb5bd !important;
}
.genreTagOFF {
    color: #fff !important;
    padding: 10px 20px;
    background-color: #ef233c;
    border-radius: 20px;
    margin-left: 3px;
    margin-right: 3px;
    cursor: pointer;
    margin: 4px 5px;
}
.genreTagOFF:hover {
    background-color: #a4161a !important;
}
.container-fluid {
    padding: 0;
}
i {
    padding-left: 4px;
}
.HomeMovies {
    width: 100%;
    background-color: #0f1014;
    margin-top: 0;
    padding-top: 30px;
}
.HomeMovieGrid {
    display: grid;
    grid-gap: 15px;
    gap: 15px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

@media screen and (max-width: 560px) {
    .HomePage {
        height: 100%;
        background-image: linear-gradient(
                rgba(0, 0, 0, 0.8),
                rgba(0, 0, 0, 0.35)
            ),
            url(/static/media/wallpaper2.8717e91e.jpg);
        background-size: cover;
        background-color: #0f1014;
    }
}
@media screen and (max-width: 1080px) {
    .HomeMovieGrid {
        grid-gap: 20px;
        gap: 20px;
        grid-template-columns: 1fr 1fr 1fr;
    }
}
@media screen and (max-width: 900px) {
    .HomeMovieGrid {
        grid-gap: 12px;
        gap: 12px;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 770px) {
    .HomeMovieGrid {
        grid-gap: 20px;
        gap: 20px;
        grid-template-columns: 1fr 1fr;
    }
    .genreHeader {
        font-size: 20px;
    }
    .genreTagOFF {
        font-size: 14px;
        padding: 5px 10px;
        margin-left: 2px;
        margin-right: 2px;
    }
    .genreTagON {
        font-size: 14px;
        padding: 5px 10px;
        margin-left: 2px;
        margin-right: 2px;
        margin: 3px 5px;
    }
}

@media screen and (max-width: 560px) {
    .HomeMovieGrid {
        grid-gap: 9px;
        gap: 9px;
        grid-template-columns: 1fr 1fr;
    }
}

.Main-Card {
    position: relative;
    cursor: pointer;
    text-align: center;
    font-size: 5px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding-bottom: 5px;
}

.Poster {
    height: 355px;
    max-width: 100%;
    border: 0.002px white solid;
    transform: 0.3s;
    border-radius: 5px;
}
.Main-Card:hover {
    padding-bottom: 0;
}
.Poster:hover {
    position: relative;
    height: 357px;
    opacity: 80%;
}

.Movie-Title {
    width: 100%;
    font-size: 17px;
    padding: 8px 0;
    text-align: center;
}

.movie-infos {
    padding: 15px 0;
}
.movie-title {
    margin: 0;
    font-size: large;
    margin-top: 0;
    margin-bottom: 0;
    font-family: "Montserrat";
    padding: 0;
}

.movie-voting {
    position: absolute;
    right: 25px;
    top: 5px;
    background-color: #0f1014;
    border: 1px solid gray;
    padding: 10px;
    font-size: 12px;
    border-radius: 25px;
}
@media screen and (max-width: 950px) {
    .movie-voting {
        right: 3.5%;
    }
}
@media screen and (max-width: 560px) {
    .movie-voting {
        right: 20.981%;
    }
    .Poster {
        height: 250px;
    }
    .Poster:hover {
        position: relative;
        height: 252px;
    }
}
@media screen and (max-width: 480px) {
    .movie-voting {
        right: 5%;
    }
}

.movie-details {
    margin-top: 3%;
    font-family: "Montserrat", sans-serif;
}
.overveiwContent {
    font-size: medium;
}
.left-box {
    color: #fff !important;
    margin: 15px 0;
    padding: 20px;
    font-family: "Montserrat", sans-serif;
}
.casting {
    transition: width 0.5s, height 0.5s;
    text-decoration: none;
}
.casting a {
    text-decoration: none;
}

.casting img {
    height: 50px;
    width: 50px;
    margin-right: 10px;
    margin-bottom: 35px;
    transition: width 0.5s, height 0.5s;
}
.topTitle-Movie {
    font-weight: 700;
}
.casting img:hover {
    height: 80px;
    width: 80px;
}
.main-img {
    height: 420px;
    box-shadow: -4px 4px 5px 0 #000;
}

.recommendedMovies {
    width: 100%;
    background-color: #0f1014;
    margin-top: 4.5%;
}
.RecommendHeading {
    color: #fff;
    font-family: "Montserrat";
    padding-left: 15px;
    margin-bottom: 20px;
}

.trailer-bttn {
    margin-top: 15px;
    text-decoration: none !important;
    color: #fff !important;
    border-radius: 0.4em;
    cursor: pointer;

    font-weight: 400;
    padding: 4px 9px;
    background: #3a86ff;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    border-color: #3a86ff !important;
    transition: width 0.2s, height 0.2s;
}
.overveiwContent {
    font-size: 5vw;
}

.youtube-container {
    z-index: 99;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.trailer-bttn:hover {
    padding: 5px 12px;
}
.recommendedGrid {
    display: grid;
    grid-gap: 15px;
    gap: 15px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.MainBackGround {
    background-size: contain;
}
.close-bttn {
    z-index: 100;
    margin-top: 15px;
    text-decoration: none !important;
    color: #fff !important;
    border-radius: 0.4em;
    cursor: pointer;

    font-weight: 400;
    padding: 4px 9px;
    background: #3a86ff;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    border-color: #3a86ff !important;
    transition: width 0.2s, height 0.2s;
}
.close-bttn:hover {
    padding: 5px 12px;
}
.trailerContainer {
    position: relative;
}
.DisplayOn {
    display: flex;
    justify-content: center;
}
.DisplayOFF {
    display: none;
}
@media screen and (max-width: 1080px) {
    .recommendedGrid {
        grid-gap: 20px;
        gap: 20px;
        grid-template-columns: 1fr 1fr 1fr;
    }
    .MainBackGround {
        background-size: auto;
    }
}
@media screen and (max-width: 900px) {
    .recommendedGrid {
        grid-gap: 12px;
        gap: 12px;
        grid-template-columns: 1fr 1fr 1fr;
    }
