        display: flex;
        flex-direction: column-reverse;
    }
}

@media screen and (max-width: 560px) {
    .recommendedGrid {
        grid-gap: 9px;
        gap: 9px;
        grid-template-columns: 1fr 1fr;
    }
    .main-img {
        height: 280px;
    }
    .movie-details {
        font-size: 14px;
    }
    .MainBackGround {
        background-size: cover;
    }
    .casting img:hover {
        height: 55px;
        width: 55px;
    }
}

